import { useLastFM } from "use-last-fm";

function App() {
  const lastFM = useLastFM("moteurdiesel22", "89eaad0b59846014a437ddf40d547cd3");

  return (
    <>
      <h1>Use-Last-Fm</h1>
      {lastFM.status === "playing" ? (
        <p>I am listening to {lastFM.song.name} by {lastFM.song.artist}</p>
      ) : (
        <p>Not listening to music right now!</p>
      )}
    </>
  );
}

export default App;